<template>
    <div class="goods-item" @click="ItemClick">
         
        <!-- <img :src="isShowImg" alt="" @load="imgLoad"> -->
        <img v-lazy="isShowImg" alt="" @load="imgLoad">
        <div class="goods-info">
           
            <p>{{GooosItem.title}}</p>
            <span class="price">{{GooosItem.price}}</span>
            <span class="collect">{{GooosItem.cfav}}</span>
          
        </div>
        
    </div>
</template>

<script>
    export default {
        props:{
            GooosItem:{
                type:Object,
                default(){
                    return {}
                }
            }
        },
        computed:{
          isShowImg(){
            return this.GooosItem.image || this.GooosItem.show.img
          }
        },
        methods:{
          imgLoad(){
            this.$bus.$emit('ItemImageLoad')

            //使用路由进行判断
            /*
              if(this.$route.path.indexOf('/home')){
                this.$bus.$emit('HomeItemImageLoad')
              }else if(this.$route.path.indexOf('/detail')){
                this.$bus.$emit('DetailItemImageLoad)
              }
            */

          },
          ItemClick(){
            this.$router.push('/detail/' + this.GooosItem.iid )

          }
        }
    }
</script>

<style scoped>
.goods-item {
    padding-bottom: 40px;
    position: relative;

    width: 48%;
  }

  .goods-item img {
    width: 100%;
    border-radius: 5px;
  }

  .goods-info {
    font-size: 12px;
    position: absolute;
    bottom: 5px;
    left: 0;
    right: 0;
    overflow: hidden;
    text-align: center;
  }

  .goods-info p {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    margin-bottom: 3px;
  }

  .goods-info .price {
    color: var(--color-high-text);
    margin-right: 20px;
  }

  .goods-info .collect {
    position: relative;
  }

  .goods-info .collect::before {
    content: '';
    position: absolute;
    left: -15px;
    top: -1px;
    width: 14px;
    height: 14px;
    background: url("~assets/img/common/collect.svg") 0 0/14px 14px;
  }

</style>